.floating-button {
  position: absolute;
  bottom: 20px;
  font-size: 15px;
  right: 30px;
  padding: 10px;
  cursor: pointer;
  -webkit-box-shadow: 0 4px 12px 0 #98a0b4;
          box-shadow: 0 4px 12px 0 #98a0b4;
  background-image: -webkit-gradient(linear, left top, right top, from(#E040FB), to(#00BCD4));
  background-image: -webkit-linear-gradient(left, #E040FB, #00BCD4);
  background-image: -o-linear-gradient(left, #E040FB, #00BCD4);
  background-image: linear-gradient(to right, #E040FB, #00BCD4);
  color: white;
  border-radius: 5px;
  letter-spacing: 4px;
  text-transform: uppercase;
  -webkit-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease; }



